body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.game {
  background-color: #232323;
  min-height: 100vh;
  margin-left: calc(100vw - 100%);
  width: 100%;
  overflow-x: hidden;
  /* flex: 1; */ }

.title {
  color: white;
  background-color: #232323;
  font-family: "Times New Roman";
  font-size: 40px;
  font-weight: 200;
  text-align: center;
  /* text-shadow: 5px 5px 15px rgba(0, 0, 0, .5); */ }

.right.round {
  float: right;
  font-size: 20px;
  text-align: center;
  margin-right: 25px; }

.number {
  font-size: 40px;
  margin-bottom: none; }

.icon {
  font-size: 40px;
  margin-bottom: 7px;
  color: #0accab; }

.username {
  color: white;
  background-color: #4f4f4f;
  text-align: center;
  box-shadow: 5px 5px 50px rgba(0, 0, 0, 0.4); }

.slider {
  /* display: inline !important; */
  font-size: 130%;
  margin: auto !important;
  margin-top: 30px !important;
  text-align: center;
  font-size: 100%;
  color: #0accab; }

.round {
  display: inline;
  font-size: 100%;
  margin: 5px;
  color: #0accab; }

.round.current {
  font-size: 120%;
  color: white; }

.full {
  width: 100%; }

.timer {
  /* -webkit-transition:  5s; For Safari 3.1 to 6.0 */
  /* transition:  5s; */
  /* color: #0accab;
    font-family: "Times New Roman";
    font-size: 40px;
    float: right;
    margin: 10px;
    text-shadow: 5px 5px 5px rgba(0, 0, 0, .5); */ }

.waiting {
  color: white;
  text-align: center;
  margin: 20px; }

.correct {
  background-color: #0accab !important;
  color: white !important;
  font-weight: bold !important; }

.correct > .star {
  color: white !important; }

.star {
  font-weight: bold !important;
  color: #0accab;
  vertical-align: middle;
  font-family: "Times New Roman";
  font-size: 30px;
  float: right;
  margin-top: .5%;
  margin-right: 10px; }

.curved {
  border-radius: 40px; }

form {
  margin: 0 auto;
  width: 60%; }

.text {
  color: white; }

.btn-secondary {
  background-color: #0accab;
  color: #232323;
  font-weight: 500;
  border-width: 0px;
  margin: 2px; }

.input-group {
  border-radius: 40px;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5); }

.shadow {
  box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.5); }

#username {
  border-radius: 40px;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5);
  display: block;
  overflow: hidden;
  /* padding-right:10px; */
  width: 100%; }

.join {
  border-radius: 40px;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5);
  float: right; }

.btn-secondary:enabled:hover {
  background-color: white;
  color: #232323;
  font-weight: 500;
  border-width: 0px; }

.btn-secondary:disabled {
  background-color: white;
  color: #232323;
  font-weight: 500;
  border-width: 0px; }

.btn-block {
  border-radius: 40px;
  background-color: white;
  color: #232323;
  box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.5);
  font-weight: 500;
  border-width: 0px; }

.btn-block:enabled:hover {
  background-color: #0accab;
  color: #232323;
  font-weight: 500;
  border-width: 0px; }

.btn-block:enabled:hover > .star {
  color: white; }

